<template>
  <app-layout />
</template>

<script>
import { useMeta } from '@/shared/plugins/meta/useMeta'
import { useRouter } from '@/shared/plugins/vue-router'
import AppLayout from './shared/layouts/AppLayout'
import ChatWidget from '@/client/utils/ChatWidget'

export default {
  name: 'App',
  components: { AppLayout },
  setup () {
    const router = useRouter()
    useMeta()

    router.beforeEach((to, from, next) => {
      if (to.meta.layout === 'ClientLayout') {
        ChatWidget.init()
      } else {
        ChatWidget.destroy()
      }
      next()
    })
  }
}
</script>
