export const appendScript = (url) => {
  try {
    return new Promise(resolve => {
      const script = document.createElement('script')
      script.onload = () => {
        resolve(true)
      }
      script.async = true
      script.src = url
      document.head.appendChild(script)
    })
  } catch (e) {
    return false
  }
}
export const removeScript = (url) => {
  try {
    const recaptchaScript = document.getElementById(url)
    recaptchaScript.remove()
    return true
  } catch (e) {
    return false
  }
}
