import { appendScript, removeScript } from '@/client/utils/appendScript'

const chatUrl = './lib/chat.min.js'

class ChatWidget {
  isSet = false
  init () {
    if (this.isSet) return
    appendScript(chatUrl)
    this.isSet = true
  }

  destroy () {
    if (!this.isSet) return

    this.isSet = false
    const chat = document.getElementById('gorgias-chat-container')
    if (chat) chat.remove()

    removeScript(chatUrl)
  }
}

export default new ChatWidget()
