<template>
  <component :is="layout" />
</template>

<script>
import { useRoute } from 'vue-router'
import { useViewport } from '@/shared/utils/useViewport'
import { computed, defineAsyncComponent } from 'vue'

export default {
  name: 'AppLayout',
  components: {
    Admin: defineAsyncComponent(() => import('@/admin/layouts/Admin')),
    AdminAuth: defineAsyncComponent(() => import('@/admin/layouts/Auth')),
    AdminBanner: defineAsyncComponent(() => import('@/admin/layouts/Banner')),
    ClientLayout: defineAsyncComponent(() => import('@/client/layouts/DefaultLayout/DefaultLayout'))
  },
  setup () {
    const route = useRoute()
    useViewport()

    const layout = computed(() => {
      if (route.meta?.layout) {
        return route.meta.layout
      }

      return 'ClientLayout'
    })

    return { layout }
  }
}
</script>
